import React from "react";
import "./Detail.css";

class Detail extends React.Component {
    componentDidMount() {
        const { location, history } = this.props;
        if(location.state === undefined) {
            history.push("/");
        }
    }
    render() {
        const {location} = this.props;
        if(location.state) {
            return (
                <>
                    <div className="ditail__back_img"><img src={location.state.background_image} alt={location.state.title} title={location.state.title}></img></div>
                    <img className="movie__poster ditail" src={location.state.poster} alt={location.state.title} title={location.state.title}></img>
                    <div className="movie__info_container">
                        <h1 className="ditail__title">{location.state.title}</h1>
                        <div className="movie__rating ditail" id={location.state.rating} title={location.state.rating}>
                            <i className="star"></i>
                            <i className="star"></i>
                            <i className="star"></i>
                            <i className="star"></i>
                            <i className="star"></i>
                        </div>
                        <ul className="movie__genres ditail">
                            {location.state.genres.map((genre, index) => 
                                <li key={index} className="genres__genre ditail">
                                    {genre}
                                </li>
                            )}
                        </ul>
                        <h5 className="movie__info">{location.state.year} · {location.state.runtime} minutes</h5>
                    </div>
                    <div className="movie__ditail_info_container">
                        <h3 className="movie__mini_title">Storyline</h3>
                            <p>{location.state.summary}</p>
                        <h3 className="movie__mini_title">Trailer</h3>
                        <iframe src={`https://www.youtube.com/embed/${location.state.yt_trailer_code}`} frameborder="0" allowfullscreen>
                            이 브라우저는 iframe을 지원하지 않습니다
                        </iframe>
                    </div>
                </>
            )
        } else {
            return null;
        }
    }
}

// year,
// title,
// summary,
// poster,
// genres,
// rating,
// runtime,
// yt_trailer_code
// background_image
export default Detail;