import React from "react";
import { Link } from "react-router-dom"
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({ id,year, title, summary, poster, genres, rating, runtime, yt_trailer_code, background_image }) {
    return (
      <Link to={{
          pathname: `/movie/${id}`,
          state: {
            year,
            title,
            summary,
            poster,
            genres,
            rating,
            runtime,
            yt_trailer_code,
            background_image
        }}
      }>
        <div className="movie">
          <img className="movie__poster" src={poster} alt={title} title={title} />
          <div className="movie__data">
            <div className="movie__title_group">
              <h3 className="movie__title">{title}</h3>
              <h5 className="movie__year">{year}</h5>
            </div>
            <ul className="movie__genres">
              {genres.map((genre, index) => 
                  <li key={index} className="genres__genre">
                      {genre}
                  </li>
              )}
            </ul>
              <div className="movie__rating">
                <i className="star"></i><span className="number">{rating}</span>
              </div>
            <p className="movie__summary">{summary.slice(0, 120)}...</p>
          </div>
        </div>
      </Link>
    );
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired,
    rating: PropTypes.number.isRequired,
    runtime: PropTypes.number.isRequired,
    yt_trailer_code: PropTypes.string,
    background_image: PropTypes.string.isRequired
};
export default Movie;